import React, { useState } from 'react';


export default function Assignment01() {
  const [count, setCount] = useState(0);

  const increaseCount = () => {
    setCount(count + 1);
  };

  const decreaseCount = () => {
    setCount(count - 1);
  };
  
  return (
    <div className="container mt-3">
      <div className="card">
        <div className="card-body">
          <h5 className="card-title text-center">Counter</h5>
          <p className="card-text mt-3 text-center">Count: {count}</p>
          <div className="row justify-content-center">
            <div className="col-lg-2 col-md-3">
              <button className="btn btn-primary btn-lg btn-custom" onClick={increaseCount}>Increase</button>
            </div>
            <div className="col-lg-2 col-md-3">
              <button className="btn btn-danger btn-lg btn-custom" onClick={decreaseCount}>Decrease</button>
            </div>
          </div>
        </div>
      </div>

      <button type="button" class=" my-3 mt-3 btn btn-success "> <a href="../home" className='backtoHome'>Back to home</a></button>

    </div>
  );
}
