import React, { useState } from 'react';

export default function Assignment03() {
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
  });
  const [errors, setErrors] = useState({});
  const [successMessage, setSuccessMessage] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // logic
    let errors = {};
    let isValid = true;

    if (!formData.username.trim()) {
      errors.username = 'Username is required';
      isValid = false;
    }

    // email function with requirement's
    if (!formData.email.trim()) {
      errors.email = 'Email is required';
      isValid = false;
    } else if (!/^\S+@\S+\.\S+$/.test(formData.email.trim())) {
      errors.email = 'Email is invalid';
      isValid = false;
    }

    // Password function
    if (!formData.password.trim()) {
      errors.password = 'Password is required';
      isValid = false;
    } else if (
        // !/^(?=.*\d)(?=.*[(a))-(z))])(?=.*[A-Z])(?=.*[a-z]*[A-Z]).{8,}
        // !/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-z]*[A-Z]).{8,}$/.test(
      !/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/.test(
        formData.password.trim()
      )
    ) {
      errors.password =
        'Password should be at least 8 characters long and contain at least one uppercase letter, one lowercase letter, one number, and one special character';
      isValid = false;
    }

    // confirm passworod
    if (!formData.confirmPassword.trim()) {
      errors.confirmPassword = 'Confirm Password is required';
      isValid = false;
    } else if (formData.confirmPassword.trim() !== formData.password.trim()) {
      errors.confirmPassword = 'Passwords do not match';
      isValid = false;
    }

    setErrors(errors);

    if (isValid) {
      // Log form data to console
      console.log(formData);

      // Reset 
      setFormData({
        username: '',
        email: '',
        password: '',
        confirmPassword: '',
      });

      // Show success notification
      setSuccessMessage('Form submitted successfully');

      // Giving timer
      setTimeout(() => {
        setSuccessMessage('');
      }, 1000);
    }
  };

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-lg-6">
          <form onSubmit={handleSubmit} className="p-4 rounded shadow-lg bg-light">
            <h2 className="text-center text-primary">Form Validation</h2>
            <h5 className="mb-4 text-center">Assignment - 03</h5>
            <div className="mb-3">
              <label htmlFor="username" className="form-label text-secondary fw-bold">
                Username
              </label>
              <input type="text" className={`form-control ${errors.username ? 'is-invalid' : '' }`} id="username" name="username" value={formData.username} onChange={handleChange}/>
              {errors.username && (<div className="invalid-feedback">{errors.username}</div>)}
            </div>
            <div className="mb-3">
              <label htmlFor="email" className="form-label text-secondary fw-bold">Email</label>
              <input type="email" className={`form-control ${errors.email ? 'is-invalid' : ''}`} id="email" name="email" value={formData.email} onChange={handleChange}/>
              {errors.email && (<div className="invalid-feedback">{errors.email}</div>)}
            </div>
            <div className="mb-3">
              <label htmlFor="password" className="form-label text-secondary fw-bold"> Password </label>
              <input type="password" className={`form-control ${errors.password ? 'is-invalid' : ''}`} id="password" name="password" value={formData.password} onChange={handleChange} />
              {errors.password && (<div className="invalid-feedback">{errors.password}</div>)}
            </div>
            <div className="mb-3">
              <label htmlFor="confirmPassword" className="form-label text-secondary fw-bold"> Confirm Password </label>
              <input type="password" className={`form-control ${errors.confirmPassword ? 'is-invalid' : ''}`} id="confirmPassword" name="confirmPassword" value={formData.confirmPassword} onChange={handleChange} />
              {errors.confirmPassword && (<div className="invalid-feedback"> {errors.confirmPassword} </div>)}
            </div>
            <button type="submit" className="btn btn-primary w-100 mt-3 fw-bold" style={{ backgroundColor: '#20d63e', borderColor: 'grren' }}> Submit </button>
          </form>
          {successMessage && ( <div className="alert alert-success mt-3 text-center" role="alert"> {successMessage} </div>
          )}
        </div>
      </div>
    </div>
  );
}
