import React, { useState } from 'react';

export default function Assignment02() {
  const [tasks, setTasks] = useState([]);
  const [taskInput, setTaskInput] = useState('');
  const [editIndex, setEditIndex] = useState(-1);

  const inputTask = (event) => {
    setTaskInput(event.target.value);
  };

  const addTask = () => {
    if (taskInput.trim() !== '') {
      setTasks([...tasks, taskInput]);
      setTaskInput('');
    }
  };

  const editTask = (index) => {
    setEditIndex(index);
    setTaskInput(tasks[index]);
  };

  const updateTask = () => {
    const updatedTasks = [...tasks];
    updatedTasks[editIndex] = taskInput;
    setTasks(updatedTasks);
    setEditIndex(-1);
    setTaskInput('');
  };

  const deleteTask = (index) => {
    const updatedTasks = [...tasks];
    updatedTasks.splice(index, 1);
    setTasks(updatedTasks);
  };

  const copyTask = (index) => {
    navigator.clipboard.writeText(tasks[index]);
    alert("Task copied!");
  };

  return (
    <div className="container">
      <div className="card bg-info">
        <div className="row mt-5">
          <div className="col-md-6 offset-md-3">
            <h2>To-Do List</h2>
            <h6 className="mb-4">Assignment - 02</h6>
            <form onSubmit={(e) => {
              e.preventDefault();
              if (editIndex === -1) {
                addTask();
              } else {
                updateTask();
              }
            }}>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Enter task"
                  value={taskInput}
                  onChange={inputTask}
                />
              </div>
              {editIndex === -1 ? (
                <button type="submit" className="btn btn-primary mt-3">
                  Add Task
                </button>
              ) : (
                <button type="button" className="btn btn-success mt-3" onClick={updateTask}>
                  Update Task
                </button>
              )}
            </form>
            <ul className="list-group mt-4">
              {tasks.map((task, index) => (
                <li key={index} className="list-group-item">
                  {index === editIndex ? (
                    <input type="text" className="form-control" value={taskInput} onChange={inputTask} />
                  ) : (
                    task
                  )}
                  <div className="d-flex flex-row mb-4 justify-content-left">
                    <button type="button" className="btn btn-info p-2 px-3 m-1 mt-3" onClick={() => editTask(index)}> {/* onclick i use for enter button function */}
                      Edit
                    </button>
                    <button type="button" className="btn btn-danger p-2 px-2 m-1 mt-3" onClick={() => deleteTask(index)}>
                      Delete
                    </button>
                    <button type="button" className="btn btn-warning p-2 px-2 m-1 mt-3" onClick={() => copyTask(index)}>
                      Copy
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
